.bg {
  background-color: black;
}
.container1 {
  background-color: black;
  margin: 0 6%;
  width: auto;
  height: 873px;
}

.text-center {
  text-align: center;
}

.btn {
  background-color: #0d6efd;
  color: white;
  border: none;
  border-radius: 8px;
  padding: 12px 36px;
  font-size: 16px;
  cursor: pointer;
}
.mt-5 {
  margin-top: 80px;
}
.mt-1 {
  margin-top: 20px;
}
.title {
  font-weight: bolder;
  font-size: 40px;
  margin-bottom: 0;
  color: #fff;
}

.desc {
  font-size: 18px;
  color: gray;
  margin: 8px;
}
.nf {
  margin-top: 70px;
  height: 200px;
}
.no-meetings {
  height: 300px;
}
.meeting-form {
  width: 460px;
  margin: auto;
  text-align: left;
}
.form-group {
  margin-bottom: 10px;
}
.form-control {
  margin-top: 10px;
  width: 100%;
  padding: 14px;
  font-size: 16px;
  border-radius: 8px;
  border: 1px solid lightgray;
}
.form-control:focus {
  outline: none;
  border: 1px solid #0d6efd;
  background-color: #fff;
}
.mb-3 {
  margin-bottom: 30px;
}
.meetings-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.d-none {
  display: none;
}
.my-5 {
  margin: 40px 0;
}
.loading {
  margin: auto;
}
.loading-animation {
  height: 150px;
  width: 150px;
  background-color: rgb(243, 243, 243);
  border-radius: 10px;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.form-post {
  border: #fff 1px solid;
}
